.button {
    font-size: 13px;
    font-weight: bold;
    line-height: 16px;
    text-transform: uppercase;
    border-radius: 47px;
    color: #fff;
    cursor: pointer;

    &--red {
        padding: 11px 25px;
        background: #C2003C;
        border-radius: 30px;
        transition: 0.35s;

        &:hover {
            background-color: #FF6897;
            color: #C2003C;
        }

        &:active {
            background-color: lighten(#FF6897, 10%);
        }
    }

    &--green {
        padding: 11px 25px;
        background: #338905;
        border-radius: 30px;
        transition: 0.35s;

        &:hover {
            background-color: #75BA4F;
            color: #284E14;
        }

        &:active {
            background-color: lighten(#75BA4F, 15%);
        }
    }

    &--game {
        width: 100%;
        margin-right: 5px;
        border-radius: 5px;

        &:last-child {
            margin-right: 0;
        }
    }
}

@media (max-width: 460px) {
    .button--game {
        width: auto;
        margin-right: 0;
        margin-bottom: 6px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}