.games__list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style-type: none;
    color: @white;
}

.games__item {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 263px;
    margin-right: 29px;
    margin-bottom: 29px;
    padding-top: 3px;
    padding-bottom: 6px;
    padding-left: 7px;
    padding-right: 7px;
    background-color: #284E14;
    border-radius: 6px;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.25);
    text-align: center;
    box-sizing: border-box;

    &:nth-child(4n) {
        margin-right: 0;
    }
}

.games__descr {
    display: flex;
}

.games_rating {
    text-indent: -10000px;
} 

.games__title {
    margin: 0;
    margin-bottom: 6px;
    font-size: 14px;
    font-weight: normal;
    line-height: 17px;
}

.games__action {
    display: flex;
    justify-content: space-between;
    margin-top: auto;
}

.games__image {
    margin-bottom: 5px;
    border: 2px solid #222222;
    border-radius: 4px;
    box-sizing: border-box;
}

@media (max-width: 1270px) {
    .games__list {
        justify-content: flex-start;
    }

    .games__item {
        margin-right: 70px;

        &:nth-child(4n) {
            margin-right: auto;
        }

        &:nth-child(3n) {
            margin-right: 0;
        }
    }
}

@media (max-width: 970px) {
    .games__list {
        justify-content: space-around;
    }

    .games__item {
        margin-right: 0;

        &:nth-child(3n) {
            margin-right: 0;
        }

        &:nth-child(2n) {
            margin-right: 0;
        }
    }
}

@media (max-width: @tablet-width) {
    .games__list {
        justify-content: space-around;;
    }

    .games__item {
        margin-right: 0;
        
        &:nth-child(3n) {
            margin-right: 0;
        }

        &:nth-child(4n) {
            margin-right: 0;
        }
    }
}


@media (max-width: 460px) {
    .games__action {
        flex-direction: column;
    }
}