.page-header__top {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #284E14;
}

.page-header__logo {
    position: absolute;
    top: 0;
    left: 50%;
    padding-top: 7px;
    padding-left: 29px;
    padding-right: 31px;
    background: #75BA4F;
    border: 1px solid #FFFFFF;
    border-radius: 0px 0px 30px 30px;
    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);
    transform: translateX(-50%);
}

.page-header__toggle-menu {
    display: none;
}

.page-header__toggle-menu {
    position: relative;
    width: 34px;
    height: 24px;
    background-color: transparent;
    border: none;
    cursor: pointer;

    &:focus {
        outline: 0;
    }
}

.page-header__toggle-menu--closed::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 34px;
    height: 2px;
    background-color: @white;
    box-shadow: 0 11px 0 0 @white, 0 22px 0 0 @white;
    transition: 0.15s ease-in-out;
}

.page-header__toggle-menu--opened {
    &::before,
    &::after {
        content: "";
        position: absolute;
        top: 8px;
        left: 0;
        width: 34px;
        height: 2px;
        background-color: @white;
        transition: 0.15s ease-in-out;
    }

    &::before {
        transform: rotate(45deg);
    }

    &::after {
        transform: rotate(-45deg);
    }
}

.page-header__toggle-menu--nojs {
    display: none;
}

@media (max-width: 1600px) {
    .page-header__top {
        justify-content: space-between;
    }
}

@media (max-width: @tablet-width) {
    .page-header__top {
        min-height: 24px;
        padding-top: 15px;
        padding-bottom: 15px;
    }

    .page-header__toggle-menu {
        left: 15px;
        display: inline-block;
        margin-right: auto;
    }

    .page-header__toggle-menu--opened {
        position: fixed;
        top: 15px;
        z-index: 100;
    }
}