.page-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 25px;
    background-color: #151515;
}

.page-footer .main-nav__list {
    justify-content: center;
    max-width: 1090px;
}

.page-footer .main-nav__item {
    padding-top: 0;
}

.page-footer__copyright {
    display: flex;
    flex-direction: column;
    margin-left: 54px;
}

.page-footer__copyright-text {
    line-height: 20px;
    color: #4B4B4B;
}

.page-footer__extra-info {
    margin-right: 15px;
}

.page-footer__extra-info img {
    margin-right: 53px;

    &:last-child {
        margin-right: ;
    }
}

@media (max-width: 1560px) {
    .page-footer__copyright {
        margin: 0;
        margin-bottom: 20px;
        text-align: center;
    }

    .page-footer__extra-info {
        margin: 0;
    }

    .page-footer__extra-info img:last-child {
        margin-right: 0;
    }
    
    .page-footer .main-nav__list {
        margin-bottom: 20px;
    }

    .page-footer {
    flex-direction: column;
    align-items: center;

        &::before {
            display: none;
        }
    }

    .page-footer__logo {
        margin-right: 0;
        margin-left: 0;
    }
}

@media (max-width: @tablet-width) {
    .page-footer .main-nav__item {
        margin-right: 15px;
        border: 0;

        &:last-child {
            margin-right: 0;
        }
    }

    .page-footer .main-nav__list {
        flex-direction: row;
    }

    .page-footer .main-nav__link {
        font-size: 12px;
    }
}

@media (max-width: 460px) {
    .page-footer__wrapper {
        padding-left: 5px;
        padding-right: 5px;
    }

    .page-footer__extra-info {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .page-footer__extra-info img {
        margin-right: 0;
        margin-bottom: 25px;

        &:last-child {
            margin-bottom: 0;
        }
    }
}