.content {
    margin-bottom: 40px;

    &--mb-second {
        margin-bottom: 44px;
    }

    &--mb-last {
        margin-bottom: 55px;
    }
}

.content h1 {
    margin: 0;
    margin-bottom: 13px;
    font-size: 33px;
    font-weight: bold;
    line-height: 40px;
    color: @white;
}

.content h2 {
    margin: 0;
    margin-bottom: 13px;
    font-size: 24px;
    font-weight: bold;
    line-height: 30px;
    color: @white;
}

.content img {
    margin-right: 30px;
    border-radius: 10px;
}

.conten--flex {
    display: flex;
    flex-wrap: wrap;
}

.conten--flex img {
    height: 100%;
}

.conten--flex h2 {
    flex-basis: 100%;
    flex-grow: 1;
}

.conten--flex p {
    width: 73%;
}

.content p {
    margin: 0;
}

@media (max-width: 1270px) {
    .columns__content {
        padding-top: 45px;
    }

    .conten--flex p {
        width: 67%;
    }

    .content--mb-second {
        margin-bottom: 30px;
    }
}

@media (max-width: 1270px) {
    .conten--flex p {
        width: 59%;
    }
}

@media (max-width: @tablet-width) {
    .conten--flex p {
        width: 100%;
    }

    .conten--flex img {
        display: none;
    }
}

@media (max-width: 520px) {
    .content h1 {
        font-size: 24px;
    }

    .content h2 {
        font-size: 20px;
    }
}

@media (max-width: 390px) {
    .content h1 {
        font-size: 19px;
    }

    .content h2 {
        font-size: 17px;
    }
}