  body {
    background-image: none;
  }
}
.container {
  width: 1140px;
  margin: 0 auto;
  padding: 0 55px;
  background-color: #2B2B2B;
  box-shadow: 0px 4px 100px rgba(0, 0, 0, 0.25);
}
.container--main {
  padding-top: 47px;
  padding-bottom: 20px;
}
@media (max-width: 1270px) {
  .container {
    width: 930px;
    padding: 0 20px;
  }
}
@media (max-width: 970px) {
  .container {
    width: 728px;
    padding: 0 20px;
  }
}
@media (max-width: 768px) {
  .container {
    width: 100%;
