    box-sizing: border-box;
  }
}
.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page-header__top {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #284E14;
}
.page-header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  padding-top: 7px;
  padding-left: 29px;
  padding-right: 31px;
  background: #75BA4F;
  border: 1px solid #FFFFFF;
  border-radius: 0px 0px 30px 30px;
  box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.25);
  transform: translateX(-50%);
}
.page-header__toggle-menu {
  display: none;
}
.page-header__toggle-menu {
  position: relative;
  width: 34px;
  height: 24px;
  background-color: transparent;
  border: none;
  cursor: pointer;
}
.page-header__toggle-menu:focus {
  outline: 0;
}
.page-header__toggle-menu--closed::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 34px;
  height: 2px;
  background-color: #ffffff;
  box-shadow: 0 11px 0 0 #ffffff, 0 22px 0 0 #ffffff;
  transition: 0.15s ease-in-out;
}
.page-header__toggle-menu--opened::before,
.page-header__toggle-menu--opened::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 0;
  width: 34px;
  height: 2px;
  background-color: #ffffff;
  transition: 0.15s ease-in-out;
}
.page-header__toggle-menu--opened::before {
  transform: rotate(45deg);
}
.page-header__toggle-menu--opened::after {
  transform: rotate(-45deg);
}
.page-header__toggle-menu--nojs {
  display: none;
}
@media (max-width: 1600px) {
  .page-header__top {
    justify-content: space-between;
  }
}
@media (max-width: 768px) {
  .page-header__top {
    min-height: 24px;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .page-header__toggle-menu {
    left: 15px;
    display: inline-block;
    margin-right: auto;
  }
  .page-header__toggle-menu--opened {
    position: fixed;
    top: 15px;
    z-index: 100;
  }
}
.main-nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.main-nav__list {
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
  list-style-type: none;
  color: #EEEEEE;
}
.main-nav__list--left,
.main-nav__list--right {
  width: 640px;
}
.main-nav__list--left {
  justify-content: flex-end;
  margin-left: 160px;
}
.main-nav__list--right {
  justify-content: flex-start;
  margin-right: 160px;
}
.main-nav__item {
  margin-right: 48px;
  padding-top: 19px;
  padding-bottom: 16px;
}
.main-nav__item:last-child {
  margin-right: 0;
}
.main-nav__link {
  line-height: 20px;
  text-transform: uppercase;
  color: #EEEEEE;
  transition: 0.25s ease-out all;
}
.main-nav__link--active {
  color: #FFD91D;
}
.main-nav__link:hover {
  color: #FFD91D;
}
.main-nav__link:active {
  color: #dab500;
}
@media (max-width: 1900px) {
  .main-nav__list--left {
    margin-left: 60px;
  }
  .main-nav__list--right {
    margin-right: 60px;
  }
}
@media (max-width: 1730px) {
  .main-nav__list--left {
    margin-left: 10px;
  }
  .main-nav__list--right {
    margin-right: 10px;
  }
}
@media (max-width: 1600px) {
  .main-nav {
    justify-content: center;
    padding-top: 70px;
  }
  .main-nav__list--left,
  .main-nav__item {
    margin-right: 30px;
  }
}
@media (max-width: 1270px) {
  .main-nav__link {
    font-size: 14px;
  }
}
@media (max-width: 970px) {
  .main-nav__list--left,
  .main-nav__item {
    margin-right: 15px;
  }
  .main-nav__link {
    font-size: 12px;
  }
}
@media (max-width: 768px) {
  .main-nav {
    flex-direction: column;
    justify-content: flex-start;
    position: fixed;
    top: 0;
    left: 0;
    padding-top: 40px;
    background-color: #284E14;
    width: 100%;
    height: 100%;
    z-index: 99;
    transition: 0.35s ease-out all;
  }
  .main-nav__list {
    flex-direction: column;
    width: 100%;
    margin: 0;
  }
  .main-nav__item {
    margin-right: 0;
    padding: 0;
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    text-align: center;
  }
  .main-nav__link {
    display: block;
    padding-top: 15px;
    padding-bottom: 15px;
  }
  .main-nav--closed {
    left: -100%;
  }
}
