.rating {
  unicode-bidi: bidi-override;
  direction: rtl;
  margin-left: auto;
  text-align: center;
}
.rating > span {
  position: relative;
  display: inline-block;
  width: 14px;
}
.rating > span,
.rating > span ~ span {
  color: transparent;
}
.rating > span:before,
.rating > span ~ span:before {
  content: "\2605";
  position: absolute;
  left: 0;
  color: #FFD91D;
}
