.sidebar__block {
    margin-bottom: 22px;
    border-radius: 5px;

    &:last-child {
        margin-bottom: 0;
    }
}

.sidebar__block-image {
    border-radius: 10px;
}

@media (max-width: 1270px) {
    .sidebar__block--slot {
        width: 180px;
    }

    .sidebar__block-raiting {
        display: none;
    }

    .sidebar__block--casino {
        width: 180px;
    }

    .sidebar__block--casino .button {
        margin-bottom: 15px;
        width: 65%;
    }
}
