.main-nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
}

.main-nav__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 0;
    list-style-type: none;
    color: @light-text;
}

.main-nav__list--left,
.main-nav__list--right {
    width: 640px;
}

.main-nav__list--left {
    justify-content: flex-end;
    margin-left: 160px;
}

.main-nav__list--right {
    justify-content: flex-start;
    margin-right: 160px;
}

.main-nav__item {
    margin-right: 48px;
    padding-top: 19px;
    padding-bottom: 16px;

    &:last-child {
        margin-right: 0;
    }
}

.main-nav__link {
    line-height: 20px;
    text-transform: uppercase;
    color: @light-text;
    transition: 0.25s ease-out all;

    &--active {
        color: #FFD91D;
    }

    &:hover {
        color: #FFD91D;
    }

    &:active {
        color: darken(#FFD91D, 13%);
    }
}

@media (max-width: 1900px) {
    .main-nav__list--left {
        margin-left: 60px;
    }
    
    .main-nav__list--right {
        margin-right: 60px;
    }
}

@media (max-width: 1730px) {
    .main-nav__list--left {
        margin-left: 10px;
    }
    
    .main-nav__list--right {
        margin-right: 10px;
    }
}


@media (max-width: 1600px) {
    .main-nav {
        justify-content: center;
        padding-top: 70px;
    }

    .main-nav__list--left,
    .main-nav__item {
        margin-right: 30px;
    }
}

@media (max-width: 1270px) {
    .main-nav__link {
        font-size: 14px;
    }
}

@media (max-width: 970px) {
    .main-nav__list--left,
    .main-nav__item {
        margin-right: 15px;
    }

    .main-nav__link {
        font-size: 12px;
    }
}

@media (max-width: @tablet-width) {
    .main-nav {
        flex-direction: column;
        justify-content: flex-start;
        position: fixed;
        top: 0;
        left: 0;
        padding-top: 40px;
        background-color: #284E14;
        width: 100%;
        height: 100%;
        z-index: 99;
        transition: 0.35s ease-out all;
    }

    .main-nav__list {
        flex-direction: column;
        width: 100%;
        margin: 0;
    }

    .main-nav__item {
        margin-right: 0;
        padding: 0;
        border-bottom: 1px solid rgba(255, 255, 255, 0.5);
        text-align: center;
    }

    .main-nav__link {
        display: block;
        padding-top: 15px;
        padding-bottom: 15px;
    }

    .main-nav--closed {
        left: -100%;
    }
}
