.review {
    width: 100%;
    margin-bottom: 40px;
    border-collapse: separate;
    border-spacing: 0;
}

.review__head {
    font-size: 14px;
    font-weight: bold;
    line-height: 17px;
    background-color: #75BA4F;
    text-transform: uppercase;
    color: @white;
    border: 5px solid #75BA4F;
    border-radius: 5px 5px 0px 0px;
}

.review__data tr:nth-child(even) {
    background-color: #ECECEC;
}

.review__head th {
    padding-right: 4px;
    padding-top: 10px;
    padding-bottom: 12px;

    &:first-child {
        padding-left: 16px;
    }
}

.review__casino {
    width: 344px;
    text-align-last: left;
}

.review__data {
    border-radius: 6px;
    background-color: #fff;
}

.review__link {
    max-width: 190px;
    font-size: 13px;
    text-decoration: underline;
    color: #001627;
    text-align: left;

    &:hover {
        color: darken(#001627, 15%)
    }

    &:active {
        opacity: 0.5;
    }
}

.review__data td {
    position: relative;
    padding-left: 2px;
    padding-right: 2px;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
    word-wrap: break-word;
}

.review__col-wrap {
    display: flex;
    align-items: center;
    padding-left: 15px;
}

.review__col-wrap img {
    margin-right: 12px;
}

.review__number {
    margin: 0;
    margin-right: 22px;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #00334E;
}

.review__bonus-value {
    min-width: 183px;
}

.review__bonus-text {
    display: inline-block;
    font-size: 11px;
    line-height: 15px;
    color: #3E3E3E;
    text-transform: uppercase;
}

.review__bonus-sum {
    font-size: 18px;
    font-weight: bold;
    line-height: 22px;
    color: #75BA4F;
}

.review__roller {
    min-width: 187px;
}

.review__roller-value {
    font-size: 12px;
    line-height: 15px;
    color: #666666;
    text-align: center;
}

.review__action {
    min-width: 160px;
}

@media (max-width: 1270px) {
    .review__action {
        min-width: 135px;
    }

    .review__bonus-value {
        min-width: 150px;
    }

    .review__roller {
        min-width: 155px;
    }
}

@media (max-width: @tablet-width) {
    table, thead, tbody, th, td, tr {
        display: block;
    }

    .review__head {
        border: 0;
    }

    .review__data td {
        padding: 15px 10px;
    }

    .review__number {
        display: none;
    }

    .review__bonus-text {
        display: block;
    }

    thead tr {
        position: absolute;
        top: -9999px;
        left: -9999px;
    }

    tr {
        border-bottom: 2px solid #75ba4f;

        &:last-child {
            border-bottom: 0;
        }
    }

    td {
        border: none;
        border-bottom: 1px solid darken(#ECECEC, 15%);
        position: relative;
    }

    .review__col-wrap {
        justify-content: space-between;
    }

    .review__bonus-value {
        max-width: 100%;
    }

    .review .button {
        display: inline-block;
    }


    .review__data td::after {
        display: none;
    }

    .review__number {
        margin-right: 77px;
    }

    .review__col-wrap {
        padding-right: 15px;
    }
}

@media (max-width: 460px) {
    .review__col-wrap {
        justify-content: center;
    }

    .review__link {
        max-width: 145px;
        text-align: center;
    }

    .review__number {
        margin-right: 55px;
    }

    .review__col-wrap img {
        display: none;
    }
}

@media (max-width: 320px) {
    .review__number {
        margin-right: 23px;
    }
}
