.columns {
    display: flex;
    justify-content: space-between;
}

.columns__content {
    width: 848px;
}

.columns__baner {
    width: 262px;
    padding-top: 75px;
}

@media (max-width: 1270px) {
    .columns__content {
        width: 735px;
    }

    .columns__baner {
        width: 180px;
    }
}

@media (max-width: 970px) {
    .columns__content {
        width: 100%;
    }

    .columns__baner {
        display: none;
    }
}